<template>
  <div style="width: 100%;height:100%;">
    <el-button type="primary" round @click="getpredction">预测</el-button>
    <div id="main" style="width: 600px;height:400px; left: 30%"></div>
  </div>
</template>

<script>
import router from '@/router'
import * as echarts from 'echarts'
export default {
  name: 'prediction',
  data() {
    return {
      city: '',
      pre: ''
    }
  },

  created() {
    this.city = router.currentRoute.value.params.city
    const axios = require("axios");
    let _this = this;
    axios
      .get("http://localhost:8001/aqi/prediction/?city=" + router.currentRoute.value.params.city)
      .then((res) => {
        _this.pre = res.data
      })
      .catch((err) => {
      });

  },
  beforeUpdate() {
    var myChart = echarts.init(document.getElementById('main'));
    var date = new Date();
    var base = new Date(date).getTime();
    var oneDay = 24 * 3600 * 1000;
    var date = [];
    var data = [Math.random() * 300];
    var time = new Date(base);
    date.push([time.getMonth() + 1, time.getDate()].join('-'));
    for (var i = 1; i < 7; i++) {//控制需要的天数
      var now = new Date(base += oneDay); //这里控制往前一周还是往后一周
      date.push([now.getMonth() + 1, now.getDate()].join('-'));
      data.push(Math.round((Math.random() - 0.5) * 20 + data[i - 1]));
    }

    var option = {
      title: {
        x: 'center',
        text: this.city + '未来七天空气质量变化趋势'
      },
      tooltip: {},
      // legend: {
      //   data: ['AQI']
      // },
      xAxis: {
        data: date
      },
      yAxis: {},
      series: [
        {
          name: 'AQI',
          type: 'line',
          data: this.pre
        }
      ]
    };
    myChart.setOption(option);
  },
  methods: {
    getpredction() {
      var myChart = echarts.init(document.getElementById('main'));
      var date = new Date();
      var base = new Date(date).getTime();
      var oneDay = 24 * 3600 * 1000;
      var date = [];
      var data = [Math.random() * 300];
      var time = new Date(base);
      date.push([time.getMonth() + 1, time.getDate()].join('-'));
      for (var i = 1; i < 7; i++) {//控制需要的天数
        var now = new Date(base += oneDay); //这里控制往前一周还是往后一周
        date.push([now.getMonth() + 1, now.getDate()].join('-'));
        data.push(Math.round((Math.random() - 0.5) * 20 + data[i - 1]));
      }

      var option = {
        title: {
          x: 'center',
          text: this.city + '未来七天空气质量变化趋势'
        },
        tooltip: {},
        // legend: {
        //   data: ['AQI']
        // },
        xAxis: {
          data: date
        },
        yAxis: {},
        series: [
          {
            name: 'AQI',
            type: 'line',
            data: this.pre
          }
        ]
      };
      myChart.setOption(option);
    }
  },
  mounted() {
    this.getpredction()
  }
}

</script>
